.profile-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background: linear-gradient(135deg, #89f7fe, #66a6ff);
    text-align: center;
    padding: 20px;
    box-sizing: border-box;
  }
  
  .profile-card {
    background: rgba(255, 255, 255, 0.85);
    padding: 40px 60px;
    border-radius: 15px;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.1);
    /* Animations are now controlled by the 'animate' class */
  }
  
  .profile-card.animate {
    animation: fadeInFromTop 1.5s ease-out both;
  }
  
  .profile-heading {
    font-size: 3rem;
    color: #333;
    margin-bottom: 20px;
    opacity: 0;
    transform: translateY(-50px);
    /* Animations are now controlled by the 'animate' class */
  }
  
  .profile-heading.animate {
    animation: fadeInDown 1s ease-out forwards;
  }
  
  .profile-subheading {
    font-size: 1.5rem;
    color: #02b2ac;
    font-weight: bold;
    opacity: 0;
    transform: translateY(50px);
    /* Animations are now controlled by the 'animate' class */
  }
  
  .profile-subheading.animate {
    animation: fadeInUp 1s ease-out forwards 0.5s;
  }
  
  /* Animations */
  @keyframes fadeInDown {
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  @keyframes fadeInUp {
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  @keyframes fadeInFromTop {
    from {
      opacity: 0;
      transform: translateY(-100px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  /* Button Styles */
  .profile-button {
    margin-top: 30px;
    padding: 12px 24px;
    font-size: 1rem;
    color: #fff;
    background-color: #66a6ff;
    border: none;
    border-radius: 25px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .profile-button:hover {
    background-color: #89f7fe;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .profile-card {
      padding: 30px 40px;
    }
  
    .profile-heading {
      font-size: 2.5rem;
    }
  
    .profile-subheading {
      font-size: 1.2rem;
    }
  }
  
  @media (max-width: 480px) {
    .profile-card {
      padding: 20px 30px;
    }
  
    .profile-heading {
      font-size: 2rem;
    }
  
    .profile-subheading {
      font-size: 1rem;
    }
  }
  